@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@700;800&family=Outfit:wght@400;500;600&family=Dancing+Script:wght@400;600;700&display=swap');
@import url('./fonts/lovelace.css');

/* Besley Variable Font */
@font-face {
  font-family: 'Besley';
  src: url('./fonts/Besley-VariableFont_wght.ttf') format('truetype-variations');
  font-weight: 400 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Besley';
  src: url('./fonts/Besley-Italic-VariableFont_wght.ttf') format('truetype-variations');
  font-weight: 400 900;
  font-style: italic;
  font-display: swap;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    /* Brand colors - Dark theme */
    --brand-primary: #007CCA;
    --brand-accent: #D04119;
    --brand-black: #070707;
    --brand-white: #F7F7F7;
    
    /* Supportive tints */
    --blue-300: #2391E6;
    --blue-100: #BFE2FF;
    --white-70: rgba(247,247,247,.70);
    
    /* MD3-inspired spacing scale */
    --spacing-xs: 0.25rem;   /* 4px */
    --spacing-sm: 0.5rem;    /* 8px */
    --spacing-md: 1rem;      /* 16px */
    --spacing-lg: 1.5rem;    /* 24px */
    --spacing-xl: 2rem;      /* 32px */
    --spacing-2xl: 3rem;     /* 48px */
    --spacing-3xl: 4rem;     /* 64px */
    
    /* Shadcn variables for dark theme - HSL format */
    --background: 7 7 7;
    --foreground: 247 247 247;
    --card: 26 26 46;
    --card-foreground: 247 247 247;
    --popover: 0 0% 41%;
    --popover-foreground: 0 0% 100%;
    --primary: 198 100% 39%;
    --primary-foreground: 0 0% 100%;
    --secondary: 13 80% 53%;
    --secondary-foreground: 0 0% 100%;
    --muted: 0 0% 20%;
    --muted-foreground: 0 0% 64%;
    --accent: 198 100% 39%;
    --accent-foreground: 0 0% 100%;
    --destructive: 0 100% 61%;
    --destructive-foreground: 0 0% 100%;
    --border: 0 0% 100% / 0.1;
    --input: 0 0% 100% / 0.1;
    --ring: 198 100% 39%;
    --radius: 0.5rem;
  }
  
  * {
    @apply border-border;
  }
  
  body {
    @apply bg-[var(--brand-black)] text-[var(--brand-white)] font-outfit;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin: 0;
  }
  
  h1, h2, h3, h4, h5, h6 {
    @apply font-manrope font-bold;
  }
}

@layer utilities {
  .font-lovelace {
    font-family: 'Dancing Script', cursive;
  }
  
  .font-besley {
    font-family: 'Besley', serif;
  }
  
  .leading-tight-hero {
    line-height: 1.04;
  }
  
  .text-glow-blue {
    text-shadow: 0 0 18px rgba(0,124,202,.18);
  }
  
  .bg-gradient-surface {
    background: linear-gradient(180deg, rgba(247,247,247,1) 0%, rgba(247,247,247,0.98) 100%);
  }
}

/* Countdown Banner Animation */
@keyframes slideDown {
  from {
    transform: translateY(-100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(0);
    opacity: 1;
  }
  to {
    transform: translateY(-100%);
    opacity: 0;
  }
}

.animate-slideDown {
  animation: slideDown 0.3s ease-out forwards;
}

.animate-slideUp {
  animation: slideUp 0.3s ease-in forwards;
}
/* Subtle pulse animation for reduced motion support */
@keyframes pulse-subtle {
  0%, 100% {
    opacity: 0.4;
  }
  50% {
    opacity: 0.6;
  }
}

/* Burger menu animation styles */
.burger-line {
  display: block;
  width: 1.25rem;              /* 20px */
  height: 0.125rem;            /* 2px */
  border-radius: 999px;
  background: #f8fafc;         /* light line on dark bg */
  transition:
    transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  transform-origin: center center;
}

.burger-line-top-open {
  transform: translateY(8px) rotate(45deg);
  background-color: var(--brand-primary);
}

.burger-line-middle-open {
  opacity: 0;
  transform: scaleX(0.1);
}

.burger-line-bottom-open {
  transform: translateY(-8px) rotate(-45deg);
  background-color: var(--brand-primary);
}

/* Respect reduced motion preferences */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
  
  .burger-line {
    transition: none;
  }
}
.disabled-link {
  pointer-events: none;
}